.t-title {
  @include fz-64;
  color: #002868;
  text-wrap: pretty;
  position: relative;
  @include font-second;
  line-height: 1.2;
}
.head {
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
  @include max(1024px) {
    gap: 1.6rem;
  }
  &.center {
    text-align: center;
    align-items: center;
  }
  &.white {
    .t-title {
      color: var(--color-white);
    }
    .b-tag {
      --bg: rgba(255, 255, 255, 0.15);
      --cl: #fff;
      border: 0.1rem solid rgba(255, 255, 255, 0.5);
    }
  }
}
.t-center {
  text-align: center;
}
